<template>

    <div class="mypanel">

        <div class="hd">
            <div class="strong"><i class="fa fa-plus"></i>新建收货地址</div>
            <div class="des">
                <router-link to="/huiyuan/receaddressmanager">我的地址</router-link>
            </div>
        </div>

        <table class="smart-table" border="1" cellspacing="1" width="100%">



            <tr>
                <td align="right">收货人:</td>
                <td><input name="shr" v-model="receaddress.shr" placeholder="收货人"
                           v-validate="{required:true,messages:{required:'请输入收货人'}}"
                           class="input-txt"
                           type="text"/></td>

                <td align="right">电话:</td>
                <td><input name="mobile" v-validate="{required:true,messages:{required:'请输入电话'}}" v-model="receaddress.mobile" placeholder="电话"
                           class="input-txt" type="text"/></td>
            </tr>
            <tr>
                <td align="right">邮编:</td>
                <td><input name="postcode" placeholder="邮编"
                           v-validate="{required:true,zipCode:true,messages:{required:'请输入邮编',zipCode:'请输入正确的邮政编码'}}"
                           v-model="receaddress.postcode" class="input-txt" type="text"/></td>

                <td align="right">地址:</td>
                <td><input name="addinfo" v-validate="{required:true,messages:{required:'请输入地址'}}" placeholder="地址" v-model="receaddress.addinfo" class="input-txt"
                           type="text"/></td>
            </tr>

            <tr>
                <td align="right">标题:</td>
                <td colspan="3">
                    <input name="title" v-validate="{required:true,messages:{required:'请输入标题'}}" placeholder="标题" v-model="receaddress.title" class="input-txt" type="text"/>
                </td>
            </tr>

            <tr>
                <td colspan="4">

                    <el-button @click="submitHandler" type="primary" >
                        <i class="el-icon-check"></i>提交
                    </el-button>

                </td>
            </tr>
        </table>

    </div>
</template>

<script>
    export default {
        name: "receaddressadd",
        data() {
            return {
                actiontype: 'save',
                hostHead: this.$app.contextPath,
                receaddress: {
                    shr: "",
                    mobile: "",
                    hyid:this.accountInfo.id,
                    postcode: "",
                    addinfo: "",
                    title: ""
                },
            }
        },
        props:{
          accountInfo:{
              type:Object
          }
        },
        methods: {
            async load() {

                let id = this.$route.query.id;
                if (id!=null) {
                    this.actiontype = "update";
                    this.pageTitle = "编辑收货地址";
                    let url = "admin/receaddress/load";
                    let {data: res} = await this.$http.post(url, {id: id});
                    console.log("res", res);
                    if (res != null && res.data != null)
                        this.receaddress = res.data;
                }
            },
            async submitHandler() {

                const  validRes=this.myValidator.valid(this);
                console.log("valRes",validRes);
                if (!validRes)
                    return ;
                let defaultOptions = {
                    url: "admin/receaddress/save",
                    actionTip: "地址新增成功"
                };
                if (this.actiontype == "update") {
                    defaultOptions.url = "admin/receaddress/update";
                    defaultOptions.actionTip = "地址成功更新";
                }

                let params = {...this.receaddress};
                let {data: res} = await this.$http.post(defaultOptions.url, params);
                if (res.stateCode <= 0) {
                    this.$alert('提交异常', '系统提示', {
                        confirmButtonText: '确定'
                    });
                    return;
                }
                this.$message({
                    message: defaultOptions.actionTip,
                    type: 'success',
                    duration: 2000
                });
                this.$router.push("/huiyuan/receaddressmanager");
            }
        },
        created() {
            this.load();
        }


    }
</script>

<style scoped>

</style>
